export function formatNumericInput(value, allowDecimal = false) {
  if (allowDecimal) {
    // Remover caracteres no numricos nem pontos
    let numericValue = value.replace(/[^0-9.]/g, '');

    // Verificar se o ponto  o primeiro caractere
    if (numericValue.startsWith('.')) {
      numericValue = '0' + numericValue;
    }

    // Verificar se h mais de um ponto decimal e, se houver, manter apenas o primeiro
    const decimalCount = (numericValue.match(/\./g) || []).length;
    if (decimalCount > 1) {
      const parts = numericValue.split('.');
      numericValue = parts[0] + '.' + parts[1];
    }

    return numericValue;
  } else {
    // Se allowDecimal for falso, remover caracteres no numricos
    let numericValue = value.replace(/[^0-9]/g, '');

    // Verificar se h um 0 no incio seguido por outro nmero e, se houver, remover o 0
    if (numericValue.length > 1 && numericValue[0] === '0') {
      numericValue = numericValue.slice(1);
    }

    return numericValue;
  }
}